import PSO from './pso'
import getHelper from './helper'

export default function (data, MAX_ITER = 1000, BIRDS_NUM = 10) {
  // nk l VACCINE * NUMBER
  const START_TIME = data.l
  // nk o VACCINE * NUMBER
  const FINAL_TIME = data.o
  // nik w CHILDREN * VACCINE * NUMBER
  const VACCINE_NUMBER = data.w
  //  f CHILDREN
  const CHILDREN_OLD = data.f
  //  h DAY
  const SCHEDULING_ARRAY = data.h
  // 
  const W = 0.8
  // 
  const CI = 2
  // 
  const CII = 2
  const {
    initSpeedFunction,
    initPositionFunction,
    updateSpeedFunction,
    updatePositionFunction,
    fitFunction
  } = getHelper(
    START_TIME,
    FINAL_TIME,
    VACCINE_NUMBER,
    CHILDREN_OLD,
    SCHEDULING_ARRAY,
    W,
    CI,
    CII)
  return new PSO(BIRDS_NUM, MAX_ITER, {
    fitFunction,
    initSpeedFunction,
    initPositionFunction,
    updateSpeedFunction,
    updatePositionFunction
  })
}



